import React from "react";
import { Link } from "gatsby";
import CallToAction from "../master/cta";
import aboutcover1 from "../../assets/images/about/about-cover1.png";
import aboutcover2 from "../../assets/images/about/about-cover2.png";
import aboutproduct from "../../assets/images/about/about-product.png";
import splineElipseImg from "../../assets/images/about/Frame 14.svg";
import mainImg from "../../assets/images/about/Rectangle 1 (1).png";
import dotRectangle from "../../assets/images/about/Frame (27).svg";
import book from "../../assets/images/about/document-text.svg";
import wallet from "../../assets/images/about/empty-wallet-tick.svg";
import lens from "../../assets/images/about/search-favorite.svg";
import "./about.css";

const About = () => {
  return (
    <div className='home-content'>
      <div className='header-title'>
        <div className='container'>
          <div className='header-title-text'>
            <h2>
              About <span>Us</span>
            </h2>
            <ul>
              <li>
                <Link to=''>Home</Link>
              </li>
              <li>/ About Us</li>
            </ul>
          </div>
        </div>
      </div>

      <div className='about-section'>
        <div className='container'>
          <div className='about-section-page'>
            <div className='about-grid'>
              <div className='about-cover'>
                <img src={aboutcover1} alt='' />
                <span>
                  <img src={aboutcover2} alt='' />
                </span>
              </div>

              <div className='about-content'>
                <span>HOW WE FUNCTION</span>
                <h2>
                  About <span>Go Tiles</span>flooring company
                </h2>
                <p>
                  We import most of our tiles straight from the factory and sell them directly to
                  the public at wholesale prices, which has allowed Go Tiles to expand into the
                  Australian market quickly. This “high volume low margin approach” is simple but
                  hard to implement.
                </p>
                <p>
                  Almost all of our business is from referrals. We know from first-hand experience
                  that you most likely would have found out about us from friends or family and
                  after you have seen our product range and prices, experienced our service then
                  bought our tiles, you also will be so impressed you will tell others about us.
                  Over 95% of people that visit us end up buying from us and we’re sure you will
                  too.
                </p>
              </div>
            </div>
            <div className='about-product-grid'>
              <div className='about-product-content'>
                <span>WHY US</span>
                <h2>
                  The Best Quality <span>Products</span>
                </h2>
                <p>
                  We consistently endeavor to bring fresh and innovative products to the marketplace
                  through extensive research and customer feedback. The open correspondence we have
                  with both our clients and our makers empowers us to provide some of the best
                  quality products.
                </p>
              </div>

              <div className='about-product-cover'>
                <img src={aboutproduct} alt='' />
              </div>
            </div>
            <div className='about-content-coreValues'>
              <h2>
                Core <span>Values </span>
              </h2>
              <div className='aboutCoreValues-imgContainer'>
                <img src={splineElipseImg} alt='' className='coreValues-splineElipseImg' />
                <img src={mainImg} alt='' className='coreValues-mainImg' />
                <img src={dotRectangle} alt='' className='coreValues-dotRectangle' />
              </div>
              <div className='aboutCoreValues-quotes'>
                <div className='coreValues-quote'>
                  <img src={book} alt='' className='coreValues-book' />
                  <h2>We beat any written quote</h2>
                </div>
                <div className='coreValues-quote'>
                  <img src={wallet} alt='' className='coreValues-wallet' />
                  <h2>Take as you pay, we have every range in stock</h2>
                </div>
                <div className='coreValues-quote'>
                  <img src={lens} alt='' className='coreValues-lens' />
                  <h2>We will help you find your perfect tile</h2>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <CallToAction />
    </div>
  );
};
export default About;
